body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

#search-box {
  width: 100%;
  max-width: 500px;
  border: 2px solid #ccc;
  border-radius: 5px;
  margin: 20px auto;
  padding: 10px;
  font-size: 16px;
  display: block;
}

.country-list {
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.country-list li {
  width: calc(33.33% - 20px);
  background-color: #f2f2f2;
  border: 1px solid #ccc;
  border-radius: 5px;
  align-items: center;
  margin: 10px;
  padding: 10px;
  display: flex;
}

.country-list img {
  margin-right: 10px;
}

.country-info {
  max-width: 500px;
  text-align: center;
  margin: 20px auto;
}

